import { faChild, faEnvelope, faMapMarkerAlt, faRunning, faSadTear } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { useEffect, useState } from 'react'
import { Button, Col, Container, Row } from 'react-bootstrap'

function Home() {
  const [profile, setProfile] = useState({})
  const [profileLanguages, setProfileLanguages] = useState([])
  const [profileRepos, setProfileRepos] = useState([])
  const [filteredRepos, setFilteredRepos] = useState([])
  const [profileStatus, setProfileStatus] = useState(false)

  useEffect(() => {
   
    const username="aritrakrbasu"
    const password="runabasu71"
    const headers={
      "Authorization" : `Basic ${btoa(`${username}:${password}`)}`
    }
    let search = window.location.search;
    let params = new URLSearchParams(search);
    let userid = params.get('id');
    console.log(userid)
    if(userid !== null)
    {
      //profile details 
      const profile_query='https://api.github.com/users/'+userid;
      fetch(profile_query , {
        "method":"GET",
        "headers":headers
      })
        .then(res =>res.json())
        .then (json =>{
          if(json.message !== "Not Found")
          {
            setProfile(json)
          }
          else
          {
            setProfileStatus(true)
          }
          
        })

        
      //language details
      const profile_language_query='https://api.github.com/users/'+userid+'/repos';
      fetch(profile_language_query, {
        "method":"GET",
        "headers":headers
      })
        .then(res =>res.json())
        .then (json =>{
          var languages =[];
          console.log(json)
          if(json.message !=='Not Found')
          {
            json.forEach(element => {
              if(element.language!==null && !languages.includes(element.language))
              {
                languages.push(element.language)
              }
            });
            setProfileLanguages(languages)
          }
          
        })

      //repo details
      const repo_query="https://api.github.com/users/"+userid+"/repos?per_page=100&sort=created"
      fetch(repo_query, {
        "method":"GET",
        "headers":headers
      })
      .then(res =>res.json())
      .then (json =>{
        setProfileRepos(json)
        setFilteredRepos(json)
      })

    }else
    {
      setProfileStatus(true)
    }
   
  }, [])

  function handleClick(filter)
  {
    if(filter !== 'all')
    {
      const filteredRepolist = profileRepos.filter(profileRepo => profileRepo.language==filter)
      setFilteredRepos(filteredRepolist)
    }
    else
    {
      setFilteredRepos(profileRepos)
    }
  }

  return (
    <>
    {!profileStatus && (<><Container fluid>
      <Row>
        <Col>
         <div className="nav">
           <div className="nav-logo">{profile.name && profile.name.match(/\b(\w)/g)}</div>
         </div>
        </Col>
        <Col>
          <div className="nav-right">
            <a href="#"><div className="nav-btn"><FontAwesomeIcon icon={faEnvelope} /> <span>Contact me</span></div></a>
          </div>
        </Col>
      </Row>
    </Container>
      <Container fluid>
      <Row>
        <Col lg={5}>
          <div className="profile-section">
            <a href={profile.html_url}><div className="profile-username"> {'@'+profile.login} </div></a>
            <div className="profile-name"> {profile.name} </div>
            <div className="profile-image"> 
              <img src={profile.avatar_url} /> 
              <div className="profile-bg"></div>
            </div>
           
              
                {profile.location && ( 
                <div className="profile-location">
                  <div className="profile-location-icon">
                    <FontAwesomeIcon icon={faMapMarkerAlt} />
                  </div>
                <div className="profile-location-text">{profile.location}</div>
                </div>
                )}
              
            
              {profile.hireable && (<div className="profile-hire-status">AVAILABLE FOR HIRE</div>)}
          </div>
        </Col>
        <Col lg={7}>
          <div className="about-section bl">
            <div className="about-stats">
              <Container fluid>
                <Row>
                  <Col>
                    <div className ="about-profile">
                      <Row>
                        <Col sm={2} className="abouts-stats">
                          <span className="ico">
                          <FontAwesomeIcon icon={faChild} />
                          </span>
                        </Col>
                        <Col className="abouts-stats">
                          <h2> Followers</h2>
                          <h3>{profile.followers}</h3>
                        </Col>
                      </Row>
                      
                    </div>
                  </Col>
                  <Col className="bl bl-sm pl-5">
                    <div className ="about-profile">
                      <Row>
                        <Col sm={2} className="abouts-stats"><span className="ico"><FontAwesomeIcon icon={faRunning} /></span></Col>
                        <Col className="abouts-stats">
                          <h2> Following</h2>
                          <h3>{profile.following}</h3>
                        </Col>
                      </Row>
                    </div>
                  </Col>
                </Row>
              </Container>
            </div>
            <div className ="about-profile">
              {profile.bio && (
                <>
                <h1>About</h1>
                <p>
                {profile.bio}
                </p> 
                </>
              )} 
            </div>
            <div className ="about-profile">
              <h1>Languages</h1>
              <ul className="skills">
                {profileLanguages && profileLanguages.map(profileLanguage =>{
                  return(
                    <li> <img src="https://svgsilh.com/svg/26432.svg"></img> {profileLanguage}</li>
                  )
                })}
              </ul>
            </div>
           
            <div className="project-section">
              <div className ="about-profile">
                <h1>Projects</h1>
                <ul className="filter-btn">
                  <li className="filter-text">Filters :</li>
                  <Button variant="light" onClick={()=>handleClick('all')}><li>All</li></Button>
                  {profileLanguages && profileLanguages.map(profileLanguage =>{
                    return(
                      <Button variant="light" onClick={()=>handleClick(profileLanguage)}><li>{profileLanguage}</li></Button>
                    )
                  })}
                  <Button variant="light" onClick={()=>handleClick(null)}><li>Others</li></Button>
                </ul>
              </div>
              {filteredRepos.length > 0 && filteredRepos.map(filteredRepo =>{
                return(
                  <div className="project-item">
                    <a href={filteredRepo.html_url} target="_blank"><div className="project-title">{filteredRepo.name}</div></a>
                    <div className="project-link">{filteredRepo.html_url}</div>
                    <div className="project-desc">{filteredRepo.description}</div>
                    <div className="project-tags">{filteredRepo.language && (<span>{filteredRepo.language}</span>)}</div>
                  </div>
                )
              })}
            </div>
          </div>
        </Col>
      </Row>
    </Container>
    </>
    )}
    {profileStatus && (
      <div className="profile-not-found">
        <FontAwesomeIcon icon={faSadTear} />
        Profile not Found
      </div>
    )}
  </>
  )
}

export default Home
